import React from 'react'
import {useState} from 'react'
import {useDispatch,useSelector} from 'react-redux'
import {deleteCurrent} from '../Actions/Index'
import {Link} from 'react-router-dom'
import '../index.css'
import {db} from '../firebaseConfig';
import {useEffect} from 'react'

const Admin = () => {

    
    //const list = useSelector((state)=>state.adminReducers.list);
    const currlist = useSelector((state)=>state.LoginReducer.currList);
    const [input,setInput] = useState("");
    const [address,setAddress] = useState("");
    const [mob,setMob] = useState("");
    const [pass,setPass] = useState("");
    const [admin,setAdmin] = useState(false);
    const [memList,setmemList] = useState([]);
    useEffect(() => {
        getData(); 
    }, [])
   


    const dispatch = useDispatch();

    function getData(){db.collection("members").onSnapshot(
        function (querySnapshot) {
            setmemList(
            querySnapshot.docs.map((doc)=>({
                id:doc.id,
                Name:doc.data().Name,
                Mobile:doc.data().Mobile,
                Admin:doc.data().Admin,
                Password:doc.data().Password,
                Address:doc.data().Address
            })
                
            ))
        }
    )}
    

    return (
        <div className="addMembers container-fluid">
            
            
             <div className="add_item">
                            <div class="d-grid gap-2">
                                <button className='btn btn-primary' onClick={()=>dispatch(deleteCurrent(currlist[0].id))}><Link id="logOut" to="/MemberLogin">Log Out</Link></button>
                            </div>
                    <div className="accordion" id="accordionPanelsStayOpenExample">
                    <div className="accordion-item">
                        <h2 className="accordion-header" id="panelsStayOpen-headingOne">
                        <button className="accordion-button" type="button" data-bs-toggle="collapse" data-bs-target="#panelsStayOpen-collapseOne" aria-expanded="true" aria-controls="panelsStayOpen-collapseOne">
                            REGISTER NEW MEMBER
                        </button>
                        </h2>
                        <div id="panelsStayOpen-collapseOne" className="accordion-collapse collapse show" aria-labelledby="panelsStayOpen-headingOne">
                        <div className="accordion-body">
                        <div className='register'>
                  
                  <div className="mb-3">
                  <label htmlFor="i2" className="form-label">Full Name</label>
                  <input type="text" className="form-control" id="i2" placeholder="xyz" value={input} onChange={(e)=>setInput(e.target.value)}/>
                  </div>
                  <div className="mb-3">
                  <label htmlFor="i1" className="form-label">Mobile number</label>
                  <input type="text" className="form-control" id="i1" placeholder="99*******98" value={mob} onChange={(e)=>setMob(e.target.value)}/>
                  </div>
                  <div className="mb-3">
                  <label htmlFor="i3" className="form-label">Password</label>
                  <input type="text" className="form-control" id="i3" placeholder="*******" value={pass} onChange={(e)=>setPass(e.target.value)}/>
                  </div>
                  <div className="mb-3">
                  <label htmlFor="i4" className="form-label">Address</label>
                  <textarea className="form-control" id="i4" rows="3" value={address} onChange={(e)=>setAddress(e.target.value)}></textarea>
                  </div>
                  <div className="d-grid gap-2">
                  <input className="btn btn-primary" type="button" value="ADD" onClick={()=>{
                    //dispatch(addMember(input,address,mob,pass,admin),setInput(""),setAddress(""),setMob(""),setPass(""),setAdmin(""))
                    db.collection("members").add({Name:input,Address:address,Mobile:mob,Password:pass,Admin:admin});
                    setInput("");
                    setAddress("");
                    setMob("");
                    setPass("");
                    setAdmin("");
                    }}></input>
                  </div>
            </div>
            
                        </div>
                        </div>
                    </div>
                    </div>

             </div>
             <div className='registeredMember'>
                    <div className='memberData'>
                            

                                        
                            {
                                //dispatch(addMember(doc.data().Name,doc.data().Address,doc.data().Mobile,doc.data().Password,doc.data().Admin));
                            memList.map((data) => {
                                return (<div className='card dataCard' key={data.id} ><img src="https://source.unsplash.com/random" className="card-img-top" alt="img"/>
                                    <div className="listData card-body"><h5 className="card-title">{data.Name}</h5><p className="card-text">Mobile : {data.Mobile}</p><p className="card-text">Password : {data.Password}</p><p className="card-text">Admin : {(data.Admin)?"Yes":'No'}</p><p className="card-text">Address : {data.Address}</p></div><button type='button' className="btn btn-danger btn-sm" onClick={()=>db.collection('members').doc(data.id).delete()}>Delete</button></div>
                        )})}
                    </div>
             </div>

        </div>
    )
}

export default Admin
