const initial= {
    currList :[]
}


const LoginReducer=(state=initial,action)=>{
    switch(action.type){
        case "Current":
            //const{id,data}=action.payload;
            return{
                ...state,
                currList:[...state.currList,
                    action.payload
                ]
            }
        case "DeleteCurrent":
                const{id}=action.payload;
                const newList=state.currList.filter((e)=>e.id!==id)
    
                return{
                    ...state,
                    currList:newList
                }
        
        default:
            return state
    }
}

export default LoginReducer;
