export const addMember=(fname,address,mob,password,admin="No")=>{
    return{
        type:"ADD",
        payload:{
            fname: fname,
            address:address,
            mob:mob,
            password:password,
            admin:admin
        }
    }
}
export const deleteMember=(id)=>{
    return{
        type:"DELETE",
        payload:{
            id:id
        }
    }
}
export const clearMember=()=>{
    return{
        type:"CLEAR"
    }
}
export const addCurrent=(id,fname,address,mob,password,admin)=>{
    return{
        type:"Current",
        payload:{
            id:id,
            fname: fname,
            address:address,
            mob:mob,
            password:password,
            admin:admin
        }
    }
}
export const deleteCurrent=(id)=>{
    return{
        type:"DeleteCurrent",
        payload:{
            id:id
        }
    }
}