import React from 'react'
import {useSelector} from 'react-redux'
import Admin from './Admin'
import Member from './Member'
import '../index.css'
import {Link} from 'react-router-dom'
import {useEffect} from 'react';


const Account = () => {
    
    const curr = useSelector((state)=>state.LoginReducer.currList);
    const [show, setShow] = React.useState(false);
      
    useEffect(() => {
      setTimeout(() => {
        setShow(true)
      }, 5000)
    }, [show])
    
    
   
    const AD=()=>{
        if (!show) return <div id='Loader'> </div>

        else if(curr[0]!==undefined){
                
                if(curr[0].admin===true){
                    return <Admin/>
                }
                else{
                    return <Member/>
                }
            }
        else if(curr[0]===undefined) {

                return <div className='errorPage'>
                    <div className="d-grid gap-2">
                                <button className='btn btn-primary' ><Link id="logOut" to="/MemberLogin">LOGIN PAGE</Link></button>
                    </div>
                    <h3>404</h3> 
                    <p>Page not found</p>
                    <p>Enter the correct Mobile or Password</p>
                </div>
            }
    }

    return (
        <div>
            <AD/>
        </div>
    )
}

export default Account
