import React from 'react'
import '../index.css'
import {useSelector,useDispatch} from 'react-redux'
import { Link } from 'react-router-dom';
import {deleteCurrent} from '../Actions/Index';
const Member = () => {
    const data = useSelector(state => state.LoginReducer.currList[0]);
    const dispatch=useDispatch();
    return (
        <div className='memberCard'>
              <div className="card detailCard" >
                <img src="https://source.unsplash.com/random" className="card-img-top" alt="..."/>
                <div className="card-body">
                    <h5 className="card-title">{data.fname}</h5>
                    <p className="card-text">Lives in {data.address}. Can contact on {data.mob}</p>
                    <button className='btn btn-success' onClick={()=>dispatch(deleteCurrent(data.id))}><Link id="logOut" to="/MemberLogin">Log Out</Link></button>
                </div>
                </div>
        </div>
    )
}

export default Member
