const initial= {
    list :[{id:"1234567890",fname:"Mahendra Singh Dhoni",mob:"1234567890",password:"ms!dhoni123",admin:"yes",address:"Harmu Housing Colony, Ranchi, Jharkhand, India"},{id:"0987654321",fname:"Mahtab Alam",mob:"7209291966",password:"ms@mahi123",admin:"no",address:"Dhori Staff Quarter, Phusro, Bokaro, Jharkhand, India"}]
}


const adminReducers=(state=initial,action)=>{
    switch(action.type){
        case "ADD":
            //const{id,data}=action.payload;

            return{
                ...state,
                list:[...state.list,
                    action.payload
                ]
            
            
            }
            case "DELETE":
                const{id}=action.payload;
                const newList=state.list.filter((e)=>e.id!==id)
    
                return{
                    ...state,
                    list:newList
                    
                }
        
        default:
            return state
    }
}

export default adminReducers;