import React from 'react'
import {
    Link
} from "react-router-dom";
import '../index.css'
import {useState} from 'react'
//import {useDispatch,useSelector} from 'react-redux'
//import {deleteCurrent} from '../Actions/Index'

import '../index.css'
import {db,firebase} from '../firebaseConfig';
//import firebase from '../firebase';



const AdminLogin = () => {

    const [input,setInput] = useState("");
    const [address,setAddress] = useState("");
    const [mob,setMob] = useState("");
    const [pass,setPass] = useState("");
    const [admin,setAdmin] = useState(false);
    const [verify,setverify]= useState('false');
    

    /*var setupRecapcha =()=>{
        window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier('sign-in-button', {
            'size': 'invisible',
            'callback': (response) => {
              // reCAPTCHA solved, allow signInWithPhoneNumber.
              onSignInSubmit();
            }
          });
    }*/

    const onSignInSubmit=()=>{
        let recaptcha= new firebase.auth.RecaptchaVerifier('sign-in-button')
        const phoneNumber = '+91'+mob;
       // const appVerifier = window.recaptchaVerifier;
        firebase.auth().signInWithPhoneNumber(phoneNumber, recaptcha)
        .then((confirmationResult) => {
                let code = window.prompt("Enter otp");
                confirmationResult.confirm(code).then((result) => {
                // User signed in successfully.
                
                setverify('true');
                console.log("User verified");
                // ...
                }).catch((error) => {
                // User couldn't sign in (bad verification code?)
                // ...
                });
        // SMS sent. Prompt user to type the code from the message, then sign the
        // user in with confirmationResult.confirm(code).
        window.confirmationResult = confirmationResult;
        // ...
    }).catch((error) => {
      // Error; SMS not sent
      // ...
    });
    }

    //const currlist = useSelector((state)=>state.LoginReducer.currList);
    
    
    //const dispatch = useDispatch();
    return (
        <>
        <div className='memberLogin container-fluid'>
            <div className='loginForm'>
                <ul className="nav nav-pills nav-fill">
                    <li className="nav-item">
                        <Link className="nav-link" id="navMember" aria-current="page" to="/MemberLogin">LogIn</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link active" id="navAdmin" to="/SignUp">SignUp</Link>
                    </li>
                </ul>
                <div className="add_item">
                            
                    <div className="accordion" id="accordionPanelsStayOpenExample">
                    <div className="accordion-item">
                        <h2 className="accordion-header" id="panelsStayOpen-headingOne">
                        <button className="accordion-button" type="button" data-bs-toggle="collapse" data-bs-target="#panelsStayOpen-collapseOne" aria-expanded="true" aria-controls="panelsStayOpen-collapseOne">
                            REGISTER NEW MEMBER
                        </button>
                        </h2>
                        <div id="panelsStayOpen-collapseOne" className="accordion-collapse collapse show" aria-labelledby="panelsStayOpen-headingOne">
                        <div className="accordion-body">
                        <div className='register'>
                  
                  <div className="mb-3">
                  <label htmlFor="i2" className="form-label">Full Name</label>
                  <input type="text" className="form-control" id="i2" placeholder="xyz" value={input} onChange={(e)=>setInput(e.target.value)}/>
                  </div>
                  <div className="mb-3">
                  <label htmlFor="i1" className="form-label">Mobile number</label>
                  <input type="text" className="form-control" id="i1" placeholder="99*******98" value={mob} onChange={(e)=>setMob(e.target.value)}/>
                  <div id='sign-in-button'></div>
                  <button className='btn btn-primary btn-sm' type='button' onClick={onSignInSubmit}>verify</button>
                  </div>
                  <div className="mb-3">
                  <label htmlFor="i3" className="form-label">Password</label>
                  <input type="text" className="form-control" id="i3" placeholder="*******" value={pass} onChange={(e)=>setPass(e.target.value)}/>
                  </div>
                  <div className="mb-3">
                  <label htmlFor="i4" className="form-label">Address</label>
                  <textarea className="form-control" id="i4" rows="3" value={address} onChange={(e)=>setAddress(e.target.value)}></textarea>
                  </div>
                  <div className="d-grid gap-2">
                  <input className="btn btn-primary" id='add' type="button" value="ADD" onClick={()=>{
                    //dispatch(addMember(input,address,mob,pass,admin),setInput(""),setAddress(""),setMob(""),setPass(""),setAdmin(""))
                    if(verify==='true'){
                    db.collection("members").add({Name:input,Address:address,Mobile:mob,Password:pass,Admin:admin});
                    setInput("");
                    setAddress("");
                    setMob("");
                    setPass("");
                    setAdmin("");
                    }else
                        alert("Please verify your number")
                    }}></input>
                  </div>
            </div>
            
                        </div>
                        </div>
                    </div>
                    </div>

             </div>
                
             </div>
        </div>
        </>
    )
}

export default AdminLogin
